        ngn: '',
        zar: '',
        gbp: ''
      },
      showLiveRates: false,
    };
  },
  methods: {
    showRates() {
      this.showLiveRates = true;
    },
    hideRates() {
      this.showLiveRates = false;
    },
  },
  mounted() {
    this.$http
      .get(
        `https://api.coingecko.com/api/v3/simple/price?ids=bitcoin&vs_currencies=eur%2Cusd%2Caud%2CCNY%2CNGN%2CZAR%2Cgbp`
      )
      .then(function (price) {
        console.log(price);
        this.price.usd = price.body.bitcoin.usd;
        this.price.china = price.body.bitcoin.cny;
        this.price.europe = price.body.bitcoin.eur;
        this.price.australia = price.body.bitcoin.aud;
        this.price.ngn = price.body.bitcoin.ngn;
        this.price.zar = price.body.bitcoin.zar;
        this.price.gbp = price.body.bitcoin.gbp;
        console.log(this.price);
        console.log(price);
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
